
import { defineComponent } from 'vue'
import Tree from "@/shared/components/tree/tree/tree.vue";
import { COM20400112_REQ_BODY, COM20400112_RES, MenuInfo } from '@/shared/type/COM20400112.type'
import { COM20400111_REQ_BODY, COM20400111_RES } from '@/shared/type/COM20400111.type'
import { COM20400211_REQ_BODY, COM20400211_RES } from '@/shared/type/COM20400211.type'
import { COM20400122_REQ_BODY, COM20400122_RES } from '@/shared/type/COM20400122.type'
import { COM20400241_REQ_BODY, COM20400241_RES } from '@/shared/type/COM20400241.type';
import { COMMON_KEY_VALUE } from '@/shared/constants/common.const'
import { NodeData } from '@/shared/components/tree/interface'
import TreeDataService from '@/shared/services/TreeDataService'
import { modalController } from '@ionic/vue';
import BreadCrumb from '@/shared/components/BreadCrumb/BreadCrumb.vue';
const treeService = new TreeDataService();
import Pagination from '@/shared/components/Pagination/Pagination.vue'
import ModifyCard from '@/shared/components/ModifyCard/ModifyCard.vue';
import { APM20200113_RES } from '@/shared/type/APM20200113.type';
import { screenMode } from '@/shared/type/Approval.type';
export default defineComponent({
    name: "com204001sc00Vue",
    components: {
        Tree,
        Pagination,
        BreadCrumb,
        ModifyCard
    },
    data() {
        const modifyCard = {
            registerTellerName: '',
            registerTellerId: '',
            updatedTellerId: '',
            updatedTellerName: '',
            updatedDate: '',
            updatedTime: '',
            registerDate: '',
            registerTime: ''
        } as any
        return {
            modifyCard,
            isReset: false,
            COMMON_KEY_VALUE,
            selectedScreenOption: "01", // 01: WEB_URL, 02: SCREEN_ID
            searchScreenID: "",
            selectedScreen: "",
            screenList: [],
            baseScreenList: [],
            isInvalidInput: false,
            isViewMode: false,
            isAddScreen: false,
            isShowDetail: false,
            menuTree: [] as NodeData[],
            menuList: [] as MenuInfo[],
            selectedMenu: {
                menuID: "",
                menuName: "",
                menuLevel: 0,
                menuDisplayOrder: 0,
                webPageURL: "",
                parentMenuID: ""
            } as MenuInfo,
            totalMenuList: 0 as number,
            menuSize: 500 as number,
            currentPage: 1,
            pageSize: 5,
            startingIndex: 1,
            totalScreenItems: 0,
            approvalRes: {} as any,
            operationMode: 'normal' as screenMode
        }
    },
    mounted() {
        this.onGetMenuList()
        this.onGetScreenInfoList();
    },
    computed: {
        isInvalidWebURL(): boolean {
            if (this.selectedScreenOption === '01' && (this.selectedMenu.webPageURL === '' || !this.isValidUrl(this.selectedMenu.webPageURL))) return true
            return false
        },
        isInvalidSelectScreen(): boolean {
            if (this.selectedScreen === '' && this.selectedScreenOption === '02') return true
            return false
        },
        menuStatusCodeValue(): any {
            return (statusCode: string, statusValue: string) => `${statusCode}` + ' : ' + `${statusValue}`;
        },
        convertLinkedStatus() {
            return (linkedStatus: string) => linkedStatus == 'Y' ? `${this.$t('com204001sc00.linked')}` : `${this.$t('com204001sc00.not_linked')}`
        },
        modifiedCardInfo(): any {
            return {
                registerTellerName: this.selectedMenu.linkDate != '' ? this.selectedMenu.firstRegisterTellerName : '',
                registerTellerId: this.selectedMenu.linkDate != '' ? this.selectedMenu.firstRegisterTellerID : '',
                registerDate: this.selectedMenu.linkDate != '' ? this.selectedMenu.firstRegisterDate : '',
                registerTime: this.selectedMenu.linkDate != '' ? this.selectedMenu.firstRegisterTime : '',
            };
        },
        viewLinksField(): string {
            if (this.selectedScreenOption == '02') {
                return '';
            } else {
                return this.searchScreenID;
            }
        },
        approvalType(): string {
            return this.$route.query.approvalType ? this.$route.query.approvalType.toString() : ''
        },
    },
    watch: {
        selectedScreenOption(newValue) {
            if (newValue == '02') {
                this.selectedMenu.webPageURL = '';
            } else {
                this.searchScreenID = '';
                this.isInvalidInput = false;
            }
        }
    },
    methods: {
        async onGetMenuList() {
            const reqBody: COM20400112_REQ_BODY = {
                pageNumber: 1,
                pageSize: 1500
            }
            const responseData = await this.$request.getRequest('COM20400112', reqBody, false) as COM20400112_RES
            if (responseData.header.result) {
                const lists = responseData.body.list.map((list) => {
                    // Map to add subText
                    if (list.webPageURL || list.screenID) {
                        list.subText = list.webPageURL ? list.webPageURL : `(${list.screenID}) ${list.screenName}`
                    }
                    return list
                })
                // get default main Root
                let rootData = lists.filter((items: any) => items.parentMenuID == '')
                // get menu list data parent menuID is not Empty
                let menuData = lists.filter((items: any) => items.parentMenuID != '')
                this.menuList = [...rootData, ...menuData]
                this.transformAndExpandTreeData(this.menuList);
                this.approvalRequest();
            }
        },

        isValidUrl(str) {
            const pattern = new RegExp(
                /^(http:\/\/|https:\/\/)/i
            );
            return pattern.test(str);
        },

        transformAndExpandTreeData(dataList) {
            const transformData = treeService.transformIntoTreeData(dataList, 'menuID', 'parentMenuID', 'menuName', 'subText')
            this.menuTree = transformData
            treeService.expandAllNodes(this.menuTree)
        },

        diabledSelectNewMenu() {
            // Disabled select for all menu when have temp new menu
            this.menuList = this.menuList.map((menu: MenuInfo) => {
                if (menu.subText === 'Screen Name') {
                    menu.state = { indeterminate: false, checked: false, selected: true, expanded: false }
                } else {
                    menu.state = { indeterminate: false, checked: false, selected: false, expanded: false }
                }
                return menu
            });
            this.transformAndExpandTreeData(this.menuList)
        },

        async onGetScreenInfoList() {
            const reqBody: COM20400111_REQ_BODY = {
                pageNumber: this.currentPage,
                pageSize: this.pageSize,
                searchValue: this.searchScreenID,
            }
            const responseData = await this.$request.getRequest('COM20400111', reqBody, false) as COM20400111_RES
            if (responseData.header.result) {
                const lists = responseData.body.list as any;
                this.screenList = lists
                this.baseScreenList = lists
                this.totalScreenItems = responseData.body.totalCount;
            }
        },

        async onGetMenuInfoDetail(menuItem) {
            if (this.approvalType === '') {
                if (this.isAddScreen) {
                    this.diabledSelectNewMenu();
                    this.onHighLightParentNode();
                    return
                }
                const reqBody: COM20400211_REQ_BODY = {
                    menuID: menuItem.menuID
                }
                const responseData = await this.$request.getRequest('COM20400211', reqBody, false) as COM20400211_RES

                if (responseData.header.result) {
                    const menu = {
                        ...menuItem,
                        firstRegisterTellerName: responseData.body.firstRegisterTellerName,
                        firstRegisterTellerID: responseData.body.firstRegisterTellerID?.replace(/\s/g, ''),
                        firstRegisterDate: responseData.body.linkDate,
                        firstRegisterTime: responseData.body.lastChangeTime,
                        lastChangeTellerID: responseData.body.lastChangeTellerID?.replace(/\s/g, ''),
                        lastChangeTime: responseData.body.lastChangeTime,
                        lastChangeDate: responseData.body.lastChangeDate,
                        linkDate: responseData.body.linkDate,
                        menuStatusCode: this.menuStatusCodeValue(responseData.body.menuStatusCode, this.$codeConverter.convertValue("MenuStatusCode", responseData.body.menuStatusCode))  // first parameter accept menuStatusCode and second parameter accept menuStatusCode too but already convert to value
                    }
                    this.selectedMenu = menu as any;
                    this.selectedScreenOption = this.selectedMenu.webPageURL !== "" ? '01' : '02';
                    this.selectedScreen = this.selectedMenu.screenID || "";
                    this.searchScreenID = this.selectedMenu.screenID;
                    this.isViewMode = menuItem.webPageURL === "" && menuItem.screenID === "" ? false : true;
                }
            } else {
                this.diabledItemApprovalMode();
            }

        },

        statusConverter(statusCode): string {
            switch (statusCode) {
                case 'Linked':
                    return 'active';

                default:
                    return 'inactive';
            }
        },

        async onClickSearch() {
            if (this.searchScreenID == "") {
                this.pageSize = 5;
                this.currentPage = 1;
                this.isReset = true;
                this.onGetScreenInfoList();
            } else {
                this.currentPage = 1;
                this.isReset = true
                this.onGetScreenInfoList();
            }
        },

        onClickResetSearch() {
            this.pageSize = 5;
            this.currentPage = 1;
            this.isReset = true;
            this.searchScreenID = ""
            this.onGetScreenInfoList()
        },

        onClickAddScreen() {
            if (this.selectedMenu.nodes) {
                if (this.selectedMenu.parentMenuID == "") {
                    this.$dialog.onOpen({
                        dialogMode: 'Warning',
                        showCancel: false,
                        content: this.$t('com204001sc00.can_not_add_link_or_screen_to_root_menu'),
                        btnLabel: this.$t('common.check'),
                        callback: () => modalController.dismiss()
                    })
                } else {
                    this.isAddScreen = true
                    this.isShowDetail = true
                    const lists = this.menuList.map((list) => {
                        // Map to add subText
                        if (list.menuID === this.selectedMenu?.menuID) {
                            list.subText = "Screen Name.."
                            this.selectedMenu = list
                        }
                        return list
                    })
                    this.menuList = lists
                    this.transformAndExpandTreeData(this.menuList);
                    this.onHighLightParentNode();
                }
            } else {
                if (!this.selectedMenu.nodes) {
                    this.$dialog.onOpen({
                        dialogMode: 'Warning',
                        showCancel: false,
                        content: this.$t('com204001sc00.please_select_specific_menu_node_before_add_links_or_screen'),
                        btnLabel: this.$t('common.check'),
                        callback: () => modalController.dismiss()
                    })
                }
            }
        },

        onCheckValidate(): boolean {
            if (this.isInvalidWebURL || this.isInvalidSelectScreen) {
                this.isInvalidInput = true
                return true
            }
            this.isInvalidInput = false
            return false
        },

        onClickSave() {
            if (this.onCheckValidate()) {
                if (this.selectedScreenOption == '02') {
                    this.$dialog.onOpen({
                        dialogMode: "Warning",
                        content: this.$t('com204001sc00.please_select_a_screen_before_clicking_the_linked_in_button'),
                        callback: () => modalController.dismiss()
                    })
                }
            } else {
                this.$dialog.onOpen({
                    dialogMode: 'Question',
                    content: this.$t('message.would_you_like_to_save'),
                    callback: () => this.onAddScreen()
                })
            }
        },

        async onAddScreen() {
            const reqBody: COM20400122_REQ_BODY = {
                menuID: this.selectedMenu.menuID,
                screenID: this.selectedScreen,
                webPageURL: this.selectedMenu.webPageURL
            }
            const responseData = await this.$request.getRequest('COM20400122', reqBody, false) as COM20400122_RES
            if (responseData.header.result) {
                modalController.dismiss()
                this.isAddScreen = false
                this.isViewMode = true
                this.isShowDetail = true
                this.searchScreenID = ''
                this.onGetMenuList();
                this.onGetScreenInfoList();
                this.onResetHighLightParentNode();
                this.clearForm();
                this.$dialog.onOpen({
                    showCancel: false,
                    dialogMode: 'Success',
                    content: this.$t('com204001sc00.link_screen_to_menu_success'),
                    callback: () => modalController.dismiss()
                });
                this.isAddScreen = false;
                this.callbackCancel()
            }

        },

        onClickCancel() {
            this.$dialog.onOpen({
                dialogMode: 'Question',
                content: this.$t('message.are_you_sure_you_want_to_cancel_without_saving_data'),
                callback: () => this.callbackCancel(),
            })

        },

        callbackCancel() {
            this.isAddScreen = false
            this.isShowDetail = false
            const lists = this.menuList.map((list) => {
                // Map to remove subText
                if (list.menuID == this.selectedMenu?.menuID) {
                    list.subText = null as any
                    this.selectedMenu = {} as any;
                }
                return list
            })
            this.menuList = lists
            this.transformAndExpandTreeData(this.menuList);
            this.onResetHighLightParentNode();
            this.searchScreenID = ''
            this.onGetScreenInfoList();
            this.isInvalidInput = false;
            modalController.dismiss();
        },

        handlePagination(pageNumber: number) {
            this.currentPage = pageNumber;
            this.isReset = false;
            this.onGetScreenInfoList();
        },

        handleStartingIndex(startingIndex: number) {
            this.startingIndex = startingIndex
        },

        // this function I use for Tree data tranform to normal data object to using with highLight parentNode multi level
        flattenTree(tree: any) {
            const flattenedTree = [] as any
            tree.forEach((node: any) => {
                flattenedTree.push(node);
                if (node.nodes.length > 0) {
                    const childNodes = this.flattenTree(node.nodes);
                    flattenedTree.push(...childNodes);
                }
            });
            return flattenedTree;
        },

        onHighLightParentNode() {
            if (this.selectedMenu) {
                let result = this.flattenTree(this.menuTree);
                result.find((list) => {
                    if (list.menuID == this.selectedMenu.menuID) {
                        list.state.highLightParentNode = true;
                    }
                });
            }
        },

        onResetHighLightParentNode() {
            let result = this.flattenTree(this.menuTree);
            result.find((list) => {
                if (list.state.highLightParentNode) {
                    list.state.highLightParentNode = false;
                }
            });
            return result

        },

        onClickRemove() {
            if (this.selectedMenu) {
                if (!this.selectedMenu.subText && this.selectedMenu.menuName != "Root") {
                    this.$dialog.onOpen({
                        showCancel: false,
                        dialogMode: 'Warning',
                        content: this.$t('com204001sc00.this_menu_dont_have_links_or_screen_for_disconnect'),
                        callback: () => modalController.dismiss()
                    })
                } else if (this.selectedMenu.subText && this.selectedMenu.menuName != "Root") {
                    this.$dialog.onOpen({
                        dialogMode: 'Question',
                        content: this.$t('message.disconnect_from_menu'),
                        btnLabel: this.$t('common.confirm'),
                        callback: () => this.onRemoveMenu()
                    })
                } else {
                    this.$dialog.onOpen({
                        showCancel: false,
                        dialogMode: 'Warning',
                        content: this.$t('com204001sc00.it_is_default_root_folder_dont_have_option_to_linked_out'),
                        callback: () => modalController.dismiss()
                    })
                }
            }
        },
        async onRemoveMenu() {
            const reqBody: COM20400241_REQ_BODY = {
                menuID: this.selectedMenu.menuID
            }
            const responseData = await this.$request.getRequest('COM20400241', reqBody, false) as COM20400241_RES
            if (responseData.header.result) {
                modalController.dismiss();
                this.isShowDetail = false
                this.searchScreenID = ''
                this.onGetMenuList();
                this.onGetScreenInfoList();
                this.clearForm();
                this.$dialog.onOpen({
                    dialogMode: 'Success',
                    content: this.$t('com204001sc00.linked_out_success'),
                    callback: () => modalController.dismiss()
                })
            }
        },

        clearForm() {
            this.selectedMenu.menuName = '';
            this.selectedMenu.menuID = '';
            this.searchScreenID = '';
            this.selectedMenu.lastChangeTellerID = ''
            this.selectedMenu.lastChangeTellerName = ''
            this.selectedMenu.firstRegisterTellerID = ''
            this.selectedMenu.firstRegisterTellerName = '';
            this.selectedMenu.firstRegisterTime = '';
            this.selectedMenu.firstRegisterDate = ''
            this.selectedMenu.menuStatusCode = '';
            this.selectedMenu.webPageURL = ''
        },

        async approvalRequest() {
            if (this.approvalType != '') {
                await this.$approval.getAuthorizationDetail().then(async (authDetail: APM20200113_RES) => {
                    let authDetailParsed = JSON.parse(authDetail.body.requestContent)
                    this.approvalRes = authDetail.body
                    this.operationMode = this.approvalType == 'my-request' ? 'read' : this.approvalType == 'my-approval' ? 'approval' : 'normal';
                    if (this.$util.checkServiceIdType(authDetail.body.serviceID) == 'delete') {
                        const reqBody: COM20400211_REQ_BODY = {
                            menuID: authDetailParsed.menuID
                        }
                        const responseData = await this.$request.getRequest('COM20400211', reqBody, false) as COM20400211_RES
                        this.selectedMenu = {
                            menuID: responseData.body.menuID,
                            menuName: responseData.body.menuName,
                            menuLevel: responseData.body.menuLevel,
                            webPageURL: responseData.body.webPageURL,
                            screenID: `${responseData.body.screenID}(${responseData.body.screenName})`,
                            parentMenuID: responseData.body.parentMenuID,
                            firstRegisterTellerName: responseData.body.firstRegisterTellerName,
                            firstRegisterTellerID: responseData.body.firstRegisterTellerID?.replace(/\s/g, ''),
                            firstRegisterDate: responseData.body.linkDate,
                            firstRegisterTime: responseData.body.lastChangeTime,
                            lastChangeTellerID: responseData.body.lastChangeTellerID?.replace(/\s/g, ''),
                            lastChangeTime: responseData.body.lastChangeTime,
                            lastChangeDate: responseData.body.lastChangeDate,
                            linkDate: responseData.body.linkDate,
                            menuStatusCode: this.menuStatusCodeValue(responseData.body.menuStatusCode, this.$codeConverter.convertValue("MenuStatusCode", responseData.body.menuStatusCode))
                        } as any;
                        this.searchScreenID = this.selectedMenu.screenID;
                        let treeData = this.flattenTree(this.menuTree);
                        treeData.find((list) => {
                            if (list.menuID == this.selectedMenu.menuID) {
                                list.state.selected = true;
                            }
                        });
                        setTimeout(() => {
                            let treeParentClass: any = document.querySelector('#tree_active')
                            if (treeParentClass) {
                                const activeChildrenClass = treeParentClass.querySelector('.active');
                                if (activeChildrenClass) {
                                    activeChildrenClass.scrollIntoView({ behavior: "smooth", block: "center" });
                                }
                            }
                        }, 50);
                    } else {
                        const reqBody: COM20400211_REQ_BODY = {
                            menuID: authDetailParsed.menuID
                        }
                        const responseData = await this.$request.getRequest('COM20400211', reqBody, false) as COM20400211_RES
                        this.selectedMenu = {
                            menuID: authDetailParsed.menuID,
                            menuName: responseData.body.menuName,
                            webPageURL: authDetailParsed.webPageURL,
                            screenID: authDetailParsed.screenID,
                            parentMenuID: responseData.body.parentMenuID,
                            firstRegisterTellerName: responseData.body.firstRegisterTellerName,
                            firstRegisterTellerID: responseData.body.firstRegisterTellerID?.replace(/\s/g, ''),
                            firstRegisterDate: responseData.body.linkDate,
                            firstRegisterTime: responseData.body.lastChangeTime,
                            lastChangeTellerID: responseData.body.lastChangeTellerID?.replace(/\s/g, ''),
                            lastChangeTime: responseData.body.lastChangeTime,
                            lastChangeDate: responseData.body.lastChangeDate,
                            linkDate: responseData.body.linkDate,
                            menuStatusCode: this.menuStatusCodeValue(responseData.body.menuStatusCode, this.$codeConverter.convertValue("MenuStatusCode", responseData.body.menuStatusCode))
                        } as any
                        this.screenList.find((item: any) => {
                            if (item.screenID == this.selectedMenu.screenID) {
                                this.searchScreenID = `${item.screenID} ${item.screenName}`;
                            }
                        })
                        let treeData = this.flattenTree(this.menuTree);
                        treeData.find((list) => {
                            if (list.menuID == this.selectedMenu.menuID) {
                                list.state.selected = true;
                            }
                        });
                        setTimeout(() => {
                            let treeParentClass: any = document.querySelector('#tree_active');
                            if (treeParentClass) {
                                const activeChildrenClass = treeParentClass.querySelector('.active');
                                if (activeChildrenClass) {
                                    activeChildrenClass.scrollIntoView({ behavior: "smooth", block: "center" });
                                }
                            }
                        }, 50);
                    }
                });
            }
        },

        diabledItemApprovalMode() {
            this.menuList = this.menuList.map((menu: MenuInfo) => {
                if (this.selectedMenu.menuID == menu.menuID) {
                    menu.state = { indeterminate: false, checked: false, selected: true, expanded: false }
                } else {
                    menu.state = { indeterminate: false, checked: false, selected: false, expanded: false }
                }
                return menu
            });
            this.transformAndExpandTreeData(this.menuList);
        },
    },
})
